import Homepage from '~/pages/home'

export default {
	name: 'app-entry',
	data () {
		return {}
	},
	components: {
		Homepage
	},
	mounted() {
		// console.log("this.$route.path-->>", this.$route)
		if(this.$route.path == "/") {
			window.addEventListener("scroll", this.myScrollFunc);
		}
	},
	methods: {
		myScrollFunc() {
			let header = document.getElementById("header");
			var y = window.scrollY;
			if (y >= 100) {
				header.className = "header show-header"
			} else {
				header.className = "header"
			}
		}
	}
}



